import { Injectable } from '@angular/core';
import { CanActivate,ActivatedRouteSnapshot,Router } from '@angular/router';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { MsHttpService } from '../ms-http.service';
@Injectable({
  providedIn: 'root'
})
export class RouteGuardService implements CanActivate {

  constructor(private router:Router,private msService:MsHttpService) { }

  canActivate(route:ActivatedRouteSnapshot):boolean {

    if(Cookie.get('authtoken')=== "" || Cookie.get('authtoken')=== undefined || Cookie.get('authtoken')=== null )
     {
       this.router.navigate(['/']);
       return false;
     }else{
       return true
     }
  } // CanAcivate Login ends here
}
