import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { UserDashboardComponent } from './user-dashboard/user-dashboard.component';
import { AdminDashboardComponent } from './admin-dashboard/admin-dashboard.component';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CalModule } from '../calendar-modules/module';
import { FormsModule } from '@angular/forms';
import  { NgDatepickerModule } from 'ng2-datepicker';
import { NgbModule, NgbTimepicker } from '@ng-bootstrap/ng-bootstrap';
import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { AutocompleteLibModule } from 'angular-ng-autocomplete';
import { MsHttpService } from '../ms-http.service';
import { RouteGuardService } from './route-guard.service';
import { NgxUiLoaderModule } from 'ngx-ui-loader';

@NgModule({
  declarations: [UserDashboardComponent, AdminDashboardComponent],
  imports: [
    CommonModule,
    FormsModule,
    NgDatepickerModule,
    FontAwesomeModule,
    NgbModule,
    OwlDateTimeModule,
    AutocompleteLibModule,
    OwlNativeDateTimeModule,
    FontAwesomeModule,
    CalModule,
    BrowserAnimationsModule,
    NgxUiLoaderModule,
    RouterModule.forChild([
      { path:'user-dash',component:UserDashboardComponent, canActivate:[RouteGuardService] },
      { path:'admin-dash',component:AdminDashboardComponent,canActivate:[RouteGuardService] }
    ])
  ],
  exports : [],
  providers:[MsHttpService,RouteGuardService]
})
export class EventDashboardModule { }
