import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { MsHttpService } from 'src/app/ms-http.service';
import { ActivatedRouteSnapshot ,Router, ActivatedRoute} from '@angular/router';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  public emailId :any;

  constructor( private toaster: ToastrService,private msService:MsHttpService,private _route:ActivatedRoute) { }

  ngOnInit() {

  }

   resetPassword:any = () => {
    console.log(this.emailId);
    this.msService.forgotPassword({email:this.emailId})
    .subscribe((apiResponse)=>{
     if(apiResponse.status === 200){
       this.toaster.success("Reset link generated,Please check your mail");
     }else{
      this.toaster.error("Some error occured,Please try again"); 
     }
    })

  }

}
