import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http';
import { HttpErrorResponse , HttpParams } from '@angular/common/http';
import { Observable,throwError} from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { MsSocketService } from '../app/ms-socket.service'
@Injectable({
  providedIn: 'root',
})
export class MsHttpService {

  //Api URL  
  private apiUrl     = 'http://api.tulika.co.in/api/v1/';
  //private apiUrl =  "http://localhost:3000/api/v1/"; 

  constructor(private http:HttpClient ,private socketService:MsSocketService ) { } //Constructor ends here

  //SignUpFn starts  
  public signupFn(signupData):any {
  
   try {
    return this.http.post(this.apiUrl+'users/signUp',signupData);
   } catch (error) {
     return this.handleError(error);
   }
   
} //signUpFn ends here 


 //SignUpFn starts  
 public signInFn(userData):any {

  try {
    return this.http.post(this.apiUrl+'users/signIn',userData);
   } catch (error) {
     return this.handleError(error);
   }
    
} //signUpFn ends here 

public forgotPassword(email):any{
  let response = this.http.post(this.apiUrl+'users/forgotPassword',email);
  return response;
}

public resetPassword(resetData):any{
  let response = this.http.post(this.apiUrl+'users/resetpassword',resetData);
  return response;
}

public getEventsData():any{
  let response = this.http.get(this.apiUrl+'events/getAllEvents');
  return response;
}

public addNewEvent(eventData):any{
  let response = this.http.post(this.apiUrl+'events/newEvent',eventData);
  return response;
}

public updateEvent(eventData):any{
  return "Updated";
}

public getUserList():any{
  let response = this.http.get(this.apiUrl+'users/getUsersList');
  return response;
} //Getting UserList events

public getUserId(name):any{
  let response = this.http.post(this.apiUrl+'users/getUserIdByName',name);
  return response;
}

public getUserInfoFromLocalStorage = () => {
  return JSON.parse(localStorage.getItem('userData'));
} // Get User info ends here

public setUserInfoInLocalStorage = (data) => {
   localStorage.setItem('userData',JSON.stringify(data));
} // Set User info ends here

//Global error handler
private handleError(err:HttpErrorResponse){
  let errorMessage = "";
  if(err.error instanceof Error){
      errorMessage = `An Error occured. ${err.error.message}`;
  }else{
     errorMessage = `Server returned code:${err.status} error Message is :${err.error.message}`; 
  }
  return errorMessage;
} //ErrorHandler ends

} // Main class ends here
