import {
  Component,
  ChangeDetectionStrategy,
  ViewChild,
  Input,
  Output,
  OnInit,
  TemplateRef,SimpleChanges, EventEmitter
} from '@angular/core';
import {
  startOfDay,
  endOfDay,
  subDays,
  addDays,
  endOfMonth,
  isSameDay,
  isSameMonth,
  addHours,
  getTime,
  differenceInDays,
  isBefore
} from 'date-fns';
import { Subject } from 'rxjs';
import { NgbModal,ModalDismissReasons, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { faCalendarAlt,faTimes } from '@fortawesome/free-solid-svg-icons';
import {
  CalendarEvent,
  CalendarEventAction,
  CalendarEventTimesChangedEvent,
  CalendarView
} from 'angular-calendar';
import  { MsHttpService } from '../ms-http.service';


const colors: any = {
  red: {
    primary: '#ad2121',
    secondary: '#FAE3E3'
  },
  blue: {
    primary: '#1e90ff',
    secondary: '#D1E8FF'
  },
  yellow: {
    primary: '#e3bc08',
    secondary: '#FDF1BA'
  }
};

@Component({
  selector: 'mwl-calendar-component',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['styles.css'],
  templateUrl: 'template.html'
})
export class CalendarComponent {
  @ViewChild('modalContent', { static: true }) modalContent: TemplateRef<any>;

  //Passing data between other components 
  @Input() newEvents:any;
  @Input() newViewDate:Date;
  @Output() updateEventDataEmitter:EventEmitter<any> = new EventEmitter<any>();
  public updateEventData:any[];

  //Icons  
  faTimes = faTimes;

  //Showing events available on current date
  public activeDayIsOpen = true;


  //Calendar Initialising View
  view: CalendarView = CalendarView.Month;
  CalendarView = CalendarView;
  viewDate: Date = new Date();

  //Modal Data structure
  modalData: {
    date ?: Date,
    action: string;
    event: CalendarEvent;
  };
  public eventModalDataObj:any = [];

  refresh: Subject<any> = new Subject();

  //Event Data array Declaration
  public events: CalendarEvent[] = [];

  constructor(private modal: NgbModal, private http:MsHttpService) {}
  ngOnInit(){


      
  }

  ngOnChanges(changes:SimpleChanges){
    //this.events = [...this.events,this.newEvents];
    // console.log(changes.newEvents.currentValue);
    let _newEvents = changes.newEvents.currentValue;
     _newEvents.map((event)=>{
      event.start = new Date(event.start);
      event.end   = new Date(event.end);
     }) 
    this.events = _newEvents;

  
  } // Detecting new events 

  dayClicked(action: string, event: CalendarEvent): void {
    this.eventModalDataObj = [];
  let newEvents = event['day'].events;
  if(newEvents.length){
    this.pushEventData(newEvents);
  // console.log(this.eventModalDataObj);
  this.modal.open(this.modalContent, { size: 'lg' });

}

  } //dayClicked Function ends here

  pushEventData(event){
    this.eventModalDataObj = [];
    for(let ev of event){
     this.eventModalDataObj.push(ev);
 } //End for Loop
  }

  eventTimesChanged({
    event,
    newStart,
    newEnd
  }: CalendarEventTimesChangedEvent): void {
    this.events = this.events.map(iEvent => {
      if (iEvent === event) {
        return {
          ...event,
          start: newStart,
          end: newEnd
        };
      }
      return iEvent;
    });
    this.handleEvent('Dropped or resized', event);
  }

  handleEvent(action: string, event: CalendarEvent): void {

    this.updateEventData = [];
    this.updateEventData.push(event);
    this.updateEventDataEmitter.emit(this.updateEventData);

  }

  // addEvent(): void {
  //   this.events = [
  //     ...this.events,{
  //       title: 'New event',
  //       start: startOfDay(new Date()),
  //       end: endOfDay(new Date()),
  //       color: colors.red,
  //       draggable: true,
  //       resizable: {
  //         beforeStart: true,
  //         afterEnd: true
  //       }
  //     }
  //   ];
  // }

  deleteEvent(eventToDelete: CalendarEvent) {
    this.events = this.events.filter(event => event !== eventToDelete);
  }

  switchView(view: CalendarView,event?:CalendarEvent) {

      if(event){
         let selectedDate = event['day'].date;
         this.viewDate = selectedDate;
         this.view = view;
      } else if(view){
         this.view = view;
      }else{
        console.log("Some error occured")
      }
    } //End of setView

  closeOpenMonthViewDay() {
    this.activeDayIsOpen = false;
  }
}
