import { Component, OnInit,ViewChild,TemplateRef ,SimpleChanges, OnChanges} from '@angular/core';
import { NgbModal,ModalDismissReasons, NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { faCalendarAlt,faTimes,faCaretRight } from '@fortawesome/free-solid-svg-icons';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { EventEmitter } from 'events';
import { TestBed } from '@angular/core/testing';
import { MsHttpService } from 'src/app/ms-http.service';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { MsSocketService } from 'src/app/ms-socket.service';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { isBefore, isAfter, differenceInDays, distanceInWords, differenceInMinutes, addMinutes, isPast } from 'date-fns';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-admin-dashboard',
  templateUrl: './admin-dashboard.component.html',
  styleUrls: ['./admin-dashboard.component.css']
})
export class AdminDashboardComponent implements OnInit {
  //AutoComplete
  @ViewChild('clearInput',{static:true}) clearInput;  // Modal Window

  @ViewChild('newEventModal', { static: true })  // Modal Window
  newEventModal: TemplateRef<any>; // Modal window 

  //Output from Calendar Module
  public eventUpdateData :any [] ;
  public eventUdateAttendees:any = [];


  //Modal Consfiguartions
  public closeResult :string;
  public modalOption:NgbModalOptions;

  //New Event Input Variables
  public eventFormTitle :string; //Form Title
  public eventFormButtonName:string // Form Button name

  public eventId  :any;
  public startDate    = new Date();
  public endDate      = new Date();
  public eventTitle:any;
  public eventDescription:any;
  public eventLocation:any;
  public selectedAttendees:any = [];
  public calendarViewDate :Date;

  public newEvents:any = []; // New Event Array
  public updateEventButton:boolean = false; 

  public dateError:boolean = false;
  public dateErrorText:string = "";
  public loggedUser :any;

  public hideSubmitButton:boolean = false;

  //Icons
  faCalendarAlt = faCalendarAlt;
  faTimes = faTimes;
  faCaretRight = faCaretRight;

  //AutoComplete input
  public name:string;
  public userArray      :any = [];
  public userArrayClone :any = [];
  placeholder = 'Choose attendees';
  public  eventAttendees:string = "";
  
  //Search users
   public searchUsers:any = "";
   public searchUserPlaceholder:string = "Search User";
   public userWiseloadedEvents:any =[];

  constructor(private ngxLoader:NgxUiLoaderService,
              private toastr:ToastrService, 
              private modal:NgbModal,
              private msService:MsHttpService,
              private router:Router,
              private msSocketService:MsSocketService) { 
    
    //Decalring Modal Configuration options
      this.modalOption = {
        backdrop      :'static',
        backdropClass :'customBackdrop',
        size          : 'lg'
      }

  }

  ngOnInit() {
    
    this.loggedUser = this.msService.getUserInfoFromLocalStorage().username;

    this.getUsersList();
    this.loadDefaultData();

  }


  getUsersList(){

      this.msService.getUserList()
      .subscribe((apiResponse)=>{
        if(apiResponse.status === 200){
          for(let users of apiResponse.data){
            this.userArray.push(users.username);
          }
        }else{
          this.toastr.error('Couldnt fetch users from server,Please refresh the page.');
        }
      })
      this.userArrayClone = this.userArray;
  }


  loadDefaultData(){

    this.msService.getEventsData()
    .subscribe((apiResponse)=>{
     if(apiResponse.status === 200){
        this.newEvents = apiResponse.data;
     }else{
      this.toastr.error('Event Data fetching error,try again later');
     }
    })
    


  }


  public clearAutoInput(e):void{
    e.stopPropagation();
    this.clearInput.clear();
  }

  OpenUpdateForm(event:any[]){
    
    this.dateError = false;
    this.eventUpdateData = event;
    this.eventUpdateData.forEach((data,index)=>{

        //Checking attendees availability
       if(data.attendees ! == null || data.attendees ! == undefined || data.attendees.length > 0){

          //Looping through attendees and filter userArray accordingly
          data.attendees.forEach((att)=>{ 
              this.selectedAttendees.push(att);
              this.userArrayClone = this.userArrayClone.filter(user=> user !== att.name);
          })

        } 

        //Filling form with Existing Data
        this.eventId          = data.eventId;
        this.eventTitle       = data.title;
        this.startDate        = data.start;
        this.endDate          = data.end;
        this.eventLocation    = data.location;
        this.eventDescription = data.description;  

       });

       //Triggering Modal Update Form
       this.updateEventButton = true;
       this.eventFormTitle = "Update Event";
       this.eventFormButtonName = "Update";
       this.modal.open(this.newEventModal,this.modalOption);
    }

  closeNewEventModal(){
    this.hideSubmitButton = false;
    this.selectedAttendees = [];
    this.eventTitle = "";
    this.eventAttendees = "";
    this.eventDescription = "";
    this.eventLocation = "";
    this.startDate = new Date();
    this.endDate = new Date();
    this.dateErrorText = "";
    this.dateError = false;
    this.modal.dismissAll();
    this.userArrayClone = this.userArray;
    }

    
  addNewEventClick(){

    this.startDate = new Date();
    this.endDate = addMinutes(this.startDate,30);
    this.eventFormTitle = "New Event";
    this.updateEventButton = false;
    this.eventFormButtonName = "Save";
    this.modal.open(this.newEventModal,this.modalOption);
  } //addNewEventClick ends here 

   newEventSubmitted(){

    this.ngxLoader.start();  

    let submittedEvent =  {
      eventId:'',
      start: new Date(this.startDate.setSeconds(0)),
      end: new Date(this.endDate.setSeconds(0)),
      title: this.eventTitle,
      attendees : this.selectedAttendees,
      location : this.eventLocation,
      description : this.eventDescription,
      createdBy : this.loggedUser
    };

    

    // // console.log(isAfter(new Date(),submittedEvent.start));
  

      if(this.eventFormButtonName === "Save"){

        this.dateError = false;
        this.msSocketService.createNewEvent(submittedEvent);
        this.closeNewEventModal();

      } else  {
  
        submittedEvent.eventId = this.eventId;  
        let response = this.msSocketService.UpdateEvent(submittedEvent);
    
      }
      
      setTimeout(() => {
        this.loadDefaultData();
        this.closeNewEventModal();
      }, 2000);
      

      this.ngxLoader.stop(); // stop foreground spinner of the master loader with 'default' taskId
   } //newEventSubmitted 

   setEndDate(end,start){
      
    if(isPast(end)){
      this.dateError = true;
      this.dateErrorText = "Start Date is in past";
      this.hideSubmitButton = true;
    }else if((differenceInMinutes(end,start)) < 29){
      this.dateError = true;
      this.dateErrorText = "30 mins difference required between start and end date.";
      this.hideSubmitButton = true;
    }else{

      this.dateError = false;
      this.dateErrorText = "";
      this.hideSubmitButton = false;
    }
    this.endDate = addMinutes(end,30);
   }

   checkDateDiff(dt){
    let dateDifference =  differenceInMinutes(dt,this.startDate);
    if(dateDifference < 29 ){
      this.dateError = true;
      this.dateErrorText = "30 mins difference required between start and end date.";
      this.hideSubmitButton = true;
      
    }else{
      this.dateError = false;
      this.dateErrorText = "";
      this.hideSubmitButton = false;
    }
   }

   deleteEvent(eventId){
    this.ngxLoader.start();  
    let eventToDelete =  {
      eventId:eventId,
      title: this.eventTitle,
      attendees : this.selectedAttendees,
    };

    this.msSocketService.DeleteEvent(eventToDelete);
    setTimeout(() => {
      this.loadDefaultData();
      this.closeNewEventModal();
      this.ngxLoader.stop(); // stop foreground spinner of the master loader with 'default' taskId
    }, 2000); 

   }

   selectedAttendeesFn(name:any){
    let attendeesObject = { "name": name }
    //Pushing selected attendee to object
    this.selectedAttendees.push(attendeesObject);
    //Removing selected attendee from autocomplete array
    this.userArrayClone = this.userArrayClone.filter(user => user !== name); 
    //console.log(this.selectedAttendees);
   } //Selected attendess function ends here


   //Remove attendee 
   removeAttendee(removeAttendee:string){

    this.selectedAttendees = this.selectedAttendees.filter(attendee => attendee.name !== removeAttendee);
    this.userArrayClone.push(removeAttendee);

   } //Remove attendee Ends

   fetchEventsUserWise(name:string){
    let checkUser;
      this.userWiseloadedEvents = this.newEvents;
      this.userWiseloadedEvents =  this.userWiseloadedEvents.filter((data)=>{
       
        data.attendees.forEach((attendee)=>{
            if(attendee.name === name){
             checkUser = true;
            }else{
              checkUser =false;
            }   
        });
        
        return checkUser;
        
      });
        this.newEvents = this.userWiseloadedEvents;
      }

   clearUserEvents(){
    this.searchUsers = "";
    this.userWiseloadedEvents = [];
    this.loadDefaultData();
   }

   showUserwiseEvents(id:any){
     let userEventsById:any = [];
     userEventsById = this.userWiseloadedEvents.filter((event)=>event.id === id);
     this.newEvents = userEventsById;
   }

   logout(){
    localStorage.clear();
    Cookie.deleteAll();
    this.router.navigate(['/']);
  }


 } //Main Class ends here
