import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MsHttpService } from 'src/app/ms-http.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetpassword.component.html',
  styleUrls: ['./resetpassword.component.css']
})
export class ResetpasswordComponent implements OnInit {

    public password:any;
    public token:any;
  constructor(private router:Router,private route:ActivatedRoute,private msservice:MsHttpService,private toastr:ToastrService) { }

  ngOnInit() {

    this.token = this.route.snapshot.paramMap.get('token');
    console.log(this.token);

  }

  resetPassword:any = () => {
    let resetData = {
      newpass : this.password,
      token : this.token
    }
    this.msservice.resetPassword(resetData)
    .subscribe((apiResponse)=>{
      if(apiResponse.status === 200){
        this.toastr.success("Password has been changed");
        this.router.navigate(['/login']);
      }else{
        this.toastr.error(apiResponse.message);
        this.router.navigate(['/forgot-password']);
      }
    })

  }

}
